export default {
	data() {
		return {
			level:0,
			page:0,
			hasFlag:true,
			teamList:[]
		};
	},
	methods:{
		changeLv(id){
			if(id != this.level){
				this.level = id
				this.page = 0
				this.hasFlag = true
				this.teamList = []
				this.getTeamList()
			}
		},
		// 获取团队列表
		async getTeamList(){
			if(!this.hasFlag) return
			this.page = ++this.page;
			let res = await this.$fetch(this.$api.getTeam,{pageNum:this.page,pageSize:10,type:this.level},'GET')
			console.log(res);
			res.rows.forEach(item =>{
				item.createTime = this.$dayjs(item.createTime).format('YYYY-MM-DD')
			})
			this.teamList = [...this.teamList,...res.rows]
			this.hasFlag  = 10*this.page < res.total
		}
	},
	onLoad() {
		this.getTeamList()
	},
	onReachBottom() {
		this.getTeamList()
	}
}
